import React, { useState, useMemo, useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import { graphql, compose } from 'react-apollo'
import ReactGA from 'react-ga'

import { connect } from 'react-redux'

import { config, AppContext } from '@morphoto/client-common'
import datastore from '@morphoto/datastore'

import Search from '@morphoto/search'
import { dates } from '@morphoto/common'

const TABS = config.home.SEARCH_TABS

const SearchConnect = ({
  defaultTab, defaultText, fetchMore
}) => {
  const [currentTab, setCurrentTab] = useState(defaultTab || TABS.TAG)
  const { globalActions } = useContext(AppContext)
  // We separate these because one of them needs to be immediate and the
  const [searchText, setSearchText] = useState(defaultText || '')
  const [contextChoice, setContextChoice] = useState(null)

  useEffect(() => {
    ReactGA.pageview('search')
  }, [])

  useEffect(() => {
    if (contextChoice || searchText.length > 3) {
      ReactGA.event({
        category: `Search:${currentTab}`,
        action: contextChoice ? 'Search Context Select' : 'Search Text Change',
        label: contextChoice ? contextChoice.name : searchText
      })
    }
  }, [searchText, currentTab, contextChoice])

  const handleTabChange = (newTab) => {
    setContextChoice(null)
    setCurrentTab(newTab)
  }
  const handleContextChoiceChange = (newContextChoice) => {
    setContextChoice(newContextChoice)
  }
  const handleSearchTextChange = (text) => {
    setSearchText(text)
    setContextChoice(null)
  }

  let withContextQuery = null
  let withSearchQuery = null

  switch (currentTab) {
    case TABS.KEYWORD:
      withContextQuery = (Wrapped) => Wrapped
      withSearchQuery = withKeywordSearch
      break
    case TABS.TAG:
      withContextQuery = withTagSearch
      withSearchQuery = withTagSeries
      break
    case TABS.LOCATION:
      withContextQuery = withGeoCode
      // withSearchQuery = withGeoSearch
      break
    case TABS.USER:
      withContextQuery = withUserSearch
      withSearchQuery = withUserSeries
      break
    default:
      break
  }

  const ComposedSearch = useMemo(() => withSearchQuery
    ? compose(
      withContextQuery,
      withSearchQuery
    )(Search)
    : withContextQuery(Search), [withContextQuery, withSearchQuery])

  return <ComposedSearch {...{ currentTab, searchText, contextChoice }}
    actions={{
      onMapClick: globalActions.jumpToMap,
      onResultClick: globalActions.jumpToSeriesView,
      setContextChoice: handleContextChoiceChange,
      fetchMore: fetchMore,
      setCurrentTab: handleTabChange,
      setSearchText: handleSearchTextChange
    }}
  />
}
SearchConnect.propTypes = {
  searchResults: PropTypes.array,
  contextResults: PropTypes.array,
  defaultTab: PropTypes.any,
  defaultText: PropTypes.string,
  loading: PropTypes.bool,
  fetchMore: PropTypes.func
}
/*****************************************************************
 *  PART 1: SEARCH QUERIES
 *****************************************************************/

/**
 * Here's the graphql HOC that gets an individual series
 */
const withTagSearch = graphql(datastore.graphql.queries.tagSearch, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    // console.log('withTagSearch', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }
    const contextResults = data.tags ? data.tags.map(({ name, count }) => ({
      name,
      description: `~ ${count < 10 ? count : Math.ceil(count / 10) * 10} Photo series tagged`
    })) : []

    return {
      contextResults,
      loading: data.loading,
      series
    }
  },
  // skip: ({ searchText }) => !searchText || searchText.length < 3,
  options: ({ searchText }) => ({
    variables: {
      tag: !searchText || searchText.length < 3 ? '' : searchText,
      limit: 10
    }
  })
})

const withUserSearch = graphql(datastore.graphql.queries.userSearch, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    // console.log('withUserSearch', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }

    const contextResults = data.userSearch ? data.userSearch.map(({ username, gravatar, createdOn, id }) => ({
      name: username,
      id,
      description: `Joined ${dates.formatDatAgo(createdOn)}`,
      gravatar
    })) : []

    return {
      contextResults,
      loading: data.loading,
      series
    }
  },
  skip: ({ searchText }) => !searchText || searchText.length < 3,
  options: ({ searchText }) => ({
    variables: {
      searchStr: searchText,
      limit: 10
    }
  })
})

const withGeoCode = graphql(datastore.graphql.queries.geocode, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    // console.log('withGeoCode', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }

    const contextResults = data.geocoder ? data.geocoder.map(({ relevance, text, placeName, bbox, center }) => ({
      name: text,
      description: placeName,
      bbox,
      center,
      relevance
    })) : []

    return {
      contextResults,
      loading: data.loading,
      series
    }
  },
  skip: ({ searchText }) => !searchText || searchText.length < 3,
  options: ({ searchText }) => ({
    variables: {
      search: searchText
      // limit: 10
    }
  })
})

/*****************************************************************
 * PART 2: SERIES SEARCH QUERIES
 *****************************************************************/

const withTagSeries = graphql(datastore.graphql.queries.seriesWithTag, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)
    // console.log('withTagSeries', data)
    let series = null
    try {
      if (!data.loading && !data.error) series = data.series
    }
    catch (e) {
      console.error(data)
      console.error(e)
    }

    // A second Thunk function designed to get more elements in the feed history
    const fetchMore = data.loading || !data.tagSeries || !data.tagSeries.nextToken ? null : () => {
      console.log('fetchMore', data)
      data.fetchMore({
        variables: { ...data.variables, nextToken: data.tagSeries.nextToken },
        // How do we mush the new query with the old one
        updateQuery: (prev, { fetchMoreResult }) => {
          return {
            tagSeries: {
              ...fetchMoreResult.tagSeries,
              seriesSummaries: [ ...prev.tagSeries.seriesSummaries, ...fetchMoreResult.tagSeries.seriesSummaries ]
                .filter(s => s.seriesStatus !== 'deleted')
            }
          }
        }
      })
    }

    const searchResults = data.tagSeries && data.tagSeries.seriesSummaries
      ? data.tagSeries.seriesSummaries
      : []

    return {
      searchResults,
      fetchMore,
      loading: data.loading,
      series
    }
  },
  skip: ({ contextChoice }) => !contextChoice,
  options: ({ contextChoice }) => ({
    variables: {
      tag: contextChoice.name,
      limit: 10
    }
  })
})

