/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { current: { search, activeSeries, domain } } = state.home
  return {
    defaultTab: search ? search.tab : null,
    defaultText: search ? search.text : null,
    activeSeries,
    domain
  }
}

const mapDispatchToProps = dispatch => ({

})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchConnect)
