import React, { useContext, useEffect } from 'react'
import PropTypes from 'prop-types'
import ReactGA from 'react-ga'

import { graphql, compose } from 'react-apollo'
// import { print } from 'graphql/language/printer'

import { AppContext } from '@morphoto/client-common'
import datastore from '@morphoto/datastore'

import MyProfile from '@morphoto/profile'

const ProfileConnect = ({ data }) => {
  const { seriesQ, watchedSeriesQ } = data
  const { currentUser, globalActions } = useContext(AppContext)

  const mySeries = seriesQ && seriesQ.userSeries && seriesQ.userSeries.seriesSummaries
    ? seriesQ.userSeries.seriesSummaries.filter(s => s.seriesStatus !== 'deleted') || []
    : []
  const watchingSeries = watchedSeriesQ && watchedSeriesQ.arraySeries ? watchedSeriesQ.arraySeries : []
  // console.log('ProfileConnect', { mySeries, watchedSeriesQ, watchingSeries })

  const loading = Boolean(
    (seriesQ && seriesQ.loading) ||
    (watchedSeriesQ && watchedSeriesQ.loading)
  )

  useEffect(() => {
    ReactGA.pageview('profile')
  }, [])

  return <MyProfile user={currentUser} {...{
    loading,
    mySeries,
    watchingSeries,
    actions: {
      jumpToSeries: globalActions.jumpToSeriesView,
      jumpToMap: globalActions.jumpToSeriesMap
    }
  }} />
}

ProfileConnect.propTypes = {
  data: PropTypes.object, // from graphql
  jumpToSeries: PropTypes.func, // from redux
  jumpToMap: PropTypes.func // from redux
}

const withuserSeriesQuery = graphql(datastore.graphql.queries.userSeries, {
  props: ({ data, ownProps }) => {
    // print(datastore.graphql.queries.userSeries)
    console.log('ProfileConnect withuserSeriesQuery', { data, ownProps })
    return { data: { ...ownProps.data, seriesQ: data } }
  }
})

const withMyWatchedSeriesQuery = graphql(datastore.graphql.queries.seriesFromList, {
  props: ({ data, ownProps }) => {
    console.log('ProfileConnect withMyWatchedSeriesQuery', datastore.graphql.queries.seriesFromList, { data, ownProps })
    return { data: { ...ownProps.data, watchedSeriesQ: data } }
  },
  skip: ({ data, user }) => {
    try {
      return user.watching.length < 1
    }
    catch (e) {
      return true
    }
  },
  options: ({ data, user }) => {
    return {
      variables: { seriesList: user.watching }
    }
  }
})

export default compose(
  withuserSeriesQuery,
  // withMyPhotosQuery,
  withMyWatchedSeriesQuery
)(ProfileConnect)
