import React, { useState } from 'react'
// import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { connect } from 'react-redux'

import { ErrorScreen, ERROR_SEND_STATES } from '@morphoto/ui'
// import { config } from '@morphoto/client-common'

import datastore from '@morphoto/datastore'

const ErrorConnect = (props) => {
  const [state, setState] = useState(ERROR_SEND_STATES.UNSENT)
  const Wrapped = withReportBugMutation(ErrorScreen)
  return <Wrapped {...props} setState={setState} sendState={state} />
}

const withReportBugMutation = graphql(datastore.graphql.mutations.reportBug, {
  props: ({ mutate, ownProps: { reduxState, error, setState } }) => {
    console.log('withReportBugMutation props', { mutate, reduxState, error })
    if (mutate.error) setState(ERROR_SEND_STATES.ERROR)
    return {
      reportError: (note) => {
        setState(ERROR_SEND_STATES.SENDING)
        let reportJSON = ''
        // attempt to get some userAgent info
        let extra = {}
        try {
          const { userAgent, appVersion, languages, language, platform, onLine } = window.navigator
          extra = {
            userAgent,
            appVersion,
            languages,
            language,
            platform,
            onLine,
            window: {
              innerHeight: window.innerHeight,
              innerWidth: window.innerWidth
            }
          }
        }
        catch (e) {
          console.error(e)
        }
        let errSubset = {}
        try {
          errSubset = {
            message: error.message,
            stack: error.stack
          }
        }
        catch (e) {
          console.error(e)
        }

        const errObj = { note, reduxState, error: errSubset, env: process.env, ...extra }
        console.log('withReportBugMutation StartingError', errObj)
        try {
          reportJSON = JSON.stringify(errObj)
        }
        catch (e) {
          reportJSON = error.toString()
        }
        console.log('withReportBugMutation got through it', reportJSON)
        return mutate({
          variables: { reportJSON },
          update: (store, { data }) => {
            console.log('withReportBugMutation update', data)
            setState(ERROR_SEND_STATES.SENT)
          }
        })
      }
    }
  }
})

/**
 * Map our redux state and functions
 */
const mapStateToProps = (state, props) => {
  const { map, home, uploader, ...other } = state
  const newMap = map.slice(-5) // only the last 5 map states come through

  return {
    reduxState: {
      ...other,
      map: newMap,
      home
    }
  }
}

export default connect(
  mapStateToProps
)(ErrorConnect)
