import React, { useRef, useState, useEffect, useContext } from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'react-apollo'
import { AppContext } from '@morphoto/client-common'
import ReactGA from 'react-ga'

import datastore from '@morphoto/datastore'
import MyFeed from '@morphoto/feed'

const POLL_INTERVAL = 20 // (seconds)
const FEED_LIMIT = 5 // (items to load)

const FeedConnect = ({
  data, loading, loadingNewest, loadingMore, gqlActions
}) => {
  const timerRef = useRef()
  const lastCheckRef = useRef()
  const [lastCheck, setLastCheck] = useState(null)
  const { online, globalActions } = useContext(AppContext)
  lastCheckRef.current = lastCheck

  // Initiate the polling process. We simply update the timestamp every N seconds
  useEffect(() => {
    function ping () {
      if (timerRef.current) {
        console.log('FeedConnect timerRef.current', timerRef.current, POLL_INTERVAL)
        if (online.isOnline) gqlActions.fetchNewest()
        setLastCheck(String(Date.now() - POLL_INTERVAL * 1000))
      }
    }
    if (!timerRef.current) timerRef.current = setInterval(ping, POLL_INTERVAL * 1000)
    return () => {
      if (timerRef.current) {
        console.log('FeedConnect clear poll timer')
        clearInterval(timerRef.current)
        timerRef.current = null
      }
    }
  }, [gqlActions, online.isOnline])

  useEffect(() => {
    ReactGA.pageview('feed')
  }, [])

  const events = data.myFeed ? data.myFeed.feed.filter(s => s.seriesStatus !== 'deleted') || [] : []
  const window = data.myFeed ? data.myFeed.window.map(v => parseInt(v, 10)) || [null, null] : [null, null]

  return (
    <MyFeed {...{ events, loading, window }}
      actions={{
        onImageClick: globalActions.jumpToSeriesView,
        onProfileClick: globalActions.jumpToUser,
        onMapClick: globalActions.jumpToSeriesMap,
        fetchMore: gqlActions.fetchMore,
        fetchNewest: gqlActions.fetchNewest
      }}
    />
  )
}

FeedConnect.propTypes = {
  data: PropTypes.object, // from graphql
  loading: PropTypes.bool, // from graphql
  loadingNewest: PropTypes.bool, // from graphql
  loadingMore: PropTypes.bool, // from graphql
  gqlActions: PropTypes.shape({
    fetchMore: PropTypes.func,
    fetchNewest: PropTypes.func
  })
}

const withFeedQuery = graphql(datastore.graphql.queries.getMyFeed, {
  props: ({ data }) => {
    if (data.error) console.error(data.error)

    // TODO: Not sure this will propagate correctly. Test it.
    let loadingNewest = false
    let loadingMore = false

    // console.log('withFeedQuery', data)

    // One Thunk function designed for getting everything since the last check
    const fetchNewest = () => {
      loadingNewest = true
      console.log('fetchNewest', data)
      if (data.myFeed && !data.loading) {
        console.log('DEBUG fetchNewest1', { ...data.variables, start: data.myFeed.window[1] })
        data.fetchMore({
          variables: { ...data.variables, start: data.myFeed.window[1] },
          // How do we mush the new query with the old one
          updateQuery: (prev, { fetchMoreResult }) => {
            console.log('DEBUG fetchNewest2', { prev, fetchMoreResult })
            loadingNewest = false
            return {
              myFeed: {
                ...fetchMoreResult.myFeed,
                // put the newest stuff first
                feed: [ ...fetchMoreResult.myFeed.feed, ...prev.myFeed.feed ],
                window: [prev.myFeed.window[0], fetchMoreResult.myFeed.window[1]]
              }
            }
          }
        })
      }
    }

    // A second Thunk function designed to get more elements in the feed history
    const fetchMore = () => {
      loadingMore = true
      console.log('fetchMore', data)
      if (data.myFeed) {
        data.fetchMore({
          variables: { ...data.variables, end: data.myFeed.window[0] },
          // How do we mush the new query with the old one
          updateQuery: (prev, { fetchMoreResult }) => {
            loadingMore = false
            return {
              myFeed: {
                ...fetchMoreResult.myFeed,
                feed: [ ...prev.myFeed.feed, ...fetchMoreResult.myFeed.feed ],
                window: [fetchMoreResult.myFeed.window[0], prev.myFeed.window[1]]
              }
            }
          }
        })
      }
    }

    return {
      data,
      loading: data.loading,
      loadingMore,
      loadingNewest,
      gqlActions: {
        fetchNewest, // This is the poll we do every n seconds
        fetchMore // This is when we scroll to the bottom of the list
      }
    }
  },
  // skip: ({ activeSeries, isNewSeries }) => !activeSeries || isNewSeries,
  options: ({ activeSeries }) => ({
    variables: {
      limit: FEED_LIMIT
    }
  })
})

export default withFeedQuery(FeedConnect)
