import React from 'react'
import PropTypes from 'prop-types'

import Home from '@morphoto/home'

import UploaderConnect from './connectors/Uploader.connect'
import SeriesConnect from './connectors/Series.connect'
import MapConnect from './connectors/Map.connect'
import SearchConnect from './connectors/Search.connect'
import ProfileConnect from './connectors/Profile.connect'
import FeedConnect from './connectors/Feed.connect'
import OfflineMap from './connectors/OfflineMap.connect'
import {} from '@morphoto/pwainstall'

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const App = ({ installPrompt, ...props }) => {
  // We pass in the components separately so that it's easier to mock them up
  // without data connectors in cosmos
  const components = {
    upload: UploaderConnect,
    series: SeriesConnect,
    map: MapConnect,
    search: SearchConnect,
    profile: ProfileConnect,
    feed: FeedConnect,
    offline: OfflineMap,
    pwaInstall: props.installPrompt
  }

  return (
    <Home {...props} components={components} />
  )
}

App.propTypes = {
  installPrompt: PropTypes.any
}

export default App
