import React from 'react'
import ReactDOM from 'react-dom'
import AppConnect from './connectors/App.connect'
import * as serviceWorker from './serviceWorker'

// Here's our global overrides css
import '@morphoto/themes/baseline.css'
import 'mapbox-gl/dist/mapbox-gl.css'
// This is the font we need
require('typeface-roboto')

ReactDOM.render(<AppConnect />, document.getElementById('root'))

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.register()

// On android this will trick the back button into not closing the app
window.addEventListener('load', () => {
  window.history.pushState({}, '')
})

// This is our image worker to offline cache maptiles AND series photos
navigator.serviceWorker.register('imgCacheWorker.js')
